@font-face {
    font-family: 'TabacGlam';
    src: url('../assets/fonts/TabacGlamG4-Regular.woff2') format('woff2'),
        url('../assets/fonts/TabacGlamG4-Regular.woff') format('woff');
    }
nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    max-width: 100vw;
    max-height: fit-content;
    padding: 1em 2rem 1rem 2rem;
    margin: 0;
    font-size: 12px;
    line-height: 20px;
    color: #da9100;
    background-color: #1e1e1e;
    border-bottom: #0c0c0c solid 0.5px;
    font-family: 'TabacGlam', sans-serif;
    a {
        align-self: center;
        img{
            height: 40px;
            width: auto;
        }
    }
    .dropdown{
        display: inline-block;
        position: relative;
        align-self: center;
    }
    .dropdown-button{
        background-color: #da9100;
        color: #1e1e1e;
        font-size: 30px;
        border-radius: 20%;
        width: 40px;
        height: 40px;
        margin-right: 5px;
        margin-left: 10px;
    }
    .dropdown-content{
        display: none;
        position: absolute;
        width: 200px;
        overflow: hidden;
        right: 0;
        background-color: #505050;
        box-shadow: 0px 10px 10px 0px #0c0c0c;
        z-index: 100;
    }
    .dropdown:hover .dropdown-content {
        display: block;
        right: 0;
    }
    .dropdown-content a {
        font-size: 18px;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        display: block;
        color: #f5f5f5;
        padding: 5px;
        text-decoration: none;
    }
    .dropdown-content a:hover {
        color: #1e1e1e;
        background-color: #da9100;
    }
}